<template>
    <div style="width: 100%;">
        <video ref="videoPlayer" :class="['video-js', ...addClass]" :id="id"></video>
    </div>
</template>

<script>
    import videojs from 'video.js';
    import 'video.js/dist/video-js.css';

    export default {
        name: "VideoPlayer",
        props: {
            id: {
                type: String,
                default: '',
            },
            addClass: {
                type: String,
                default: '',
            },
            options: {
                type: Object,
                default() {
                    return {};
                }
            }
        },
        data() {
            return {
                player: null
            }
        },
        mounted() {
            this.player = videojs(this.$refs.videoPlayer, this.options, function onPlayerReady() {
                // console.log('onPlayerReady', this);
            });

          const settingButtons = document.getElementById(this.id);
          let settingButton = null;
            if(null !== settingButtons) {
              settingButton = settingButtons.getElementsByClassName('vjs-big-play-button')[0];
            }

            if (null !== settingButton && typeof settingButton !== 'undefined') {
                settingButton.innerHTML = '';
                settingButton.insertAdjacentHTML('afterbegin', `
                <svg class="fp-play" focusable="true" xmlns="http://www.w3.org/2000/svg" width="62.502"
                            height="62.502" viewBox="0 0 62.502 62.502">
                            <g id="Ellipse_411" data-name="Ellipse 411" fill="#4f91b3" stroke="#fff"
                                stroke-width="2" opacity="0.8">
                                <circle cx="31.251" cy="31.251" r="31.251" stroke="none" />
                                <path id="Path_697" data-name="Path 697" d="M8,5V30.736L28.221,17.868Z"
                                    transform="translate(16 14)" fill="#fff" />
                                <circle cx="31.251" cy="31.251" r="30.251" fill="none" />
                            </g>
                        </svg>
                  <svg viewBox="-2 -2 50 50" class="fp-pause" focusable="true"><use xlink:href="#fp-pause"></use></svg>
              `);
            }

          const $video = document.querySelector('.video-js');
          if(null !== $video) {
            $video.style.width = '100%';
          }
        },
        beforeDestroy() {
            if (this.player) {
                this.player.dispose()
            }
        }
    }
</script>

<style>
    .video-js .vjs-big-play-button {
        border: none;
        background: none;
        top: 50%;
        left: 50%;
        margin-top: -.81666em;
        margin-left: -1.5em;
    }

    .video-js .vjs-big-play-button:hover, .video-js:hover .vjs-big-play-button {
        border: none;
        background: none;
    }

    .video-js .vjs-big-play-button:before {
        content: '';
    }
</style>